@import "https://fonts.googleapis.com/css?family=Roboto:regular,700&display=swap";
body {
  margin: 8px;
  font-family: Roboto, sans-serif;
  position: relative;
}

.breed-select {
  margin-bottom: 40px;
}

h2 {
  margin: 0;
}

.cat-info {
  width: 1200px;
  gap: 30px;
  line-height: 1.8;
  display: flex;
}

.cat-img {
  max-width: 600px;
  max-height: 500px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cat-title {
  font-size: 38px;
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
}

.error {
  color: #ff3d00;
  font-weight: 700;
}

.hidden {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

